import theme from './theme'
import logo from './assets/logo.svg'
import { TextField, Container, Card, CardHeader, CardContent, Button, Link } from '@material-ui/core'
import { makeStyles, ThemeProvider } from '@material-ui/core/styles'

const getClasses = makeStyles((theme) => ({
    root: {
        margin: 'auto',
        width: 350,
        height: 350,
        padding: theme.spacing(2),
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
    },
    inputs: {
        width: '100%',
        margin: 0,
        padding: 0,
        '& > *': {
            ...theme.typography.h5
        }
    }
}))

function setSignInName(e){
    const signInName = document.getElementById('signInName')
    if(!signInName){
        console.error('signInName is not available')
        return
    }
    signInName.value = e.target.value
}

function setPassword(e){
    const password = document.getElementById('password')
    if(!password){
        console.error('password is not available')
        return
    }
    password.value = e.target.value
}

function signIn(){
    const signInButton = document.getElementById('next')
    if(!signInButton){
        console.error('next is not available')
        return
    }
    signInButton.click()
}

function signUp(){
    const signUpButton = document.getElementById('createAccount')
    if(!signUpButton){
        console.error('sign up is not available')
        return
    }
    signUpButton.click()
}

function SignIn() {
    const classes = getClasses();
    return(
        <Card classes={classes}>
            <div style={{width: "100%", display: 'flex', flexDirection: "column", justifyContent: 'center'}}>
                <img style={{width: 150, margin: 'auto', display: 'block'}} src={logo} alt="brighter Redact" />
            </div>
            <Container style={{margin: 0, padding: 0}}>
                <CardContent style={{width: '100%', boxSizing: 'border-box', paddingBottom: 0, paddingTop: theme.spacing(3)}}>
                    <TextField onChange={setSignInName} className={classes.inputs} variant="filled" label="Email Address" />
                </CardContent>
                <CardContent style={{width: '100%', boxSizing: 'border-box', paddingBottom: 0, paddingTop: theme.spacing(3)}}>
                    <TextField onChange={setPassword} helperText={<Link href="https://www.brighter.ai/" target="_blank">Forgot your password?</Link>} className={classes.inputs} variant="filled" label="Password" />
                </CardContent>
            </Container>
            <CardContent style={{width: '100%', boxSizing: 'border-box', display: 'grid', gridTemplateColumns: '1fr 1fr'}}>
                <Button style={{marginRight: 10}} variant="outlined" onClick={signUp} >New Account</Button>
                <Button style={{marginLeft: 10}} variant="contained" onClick={signIn} >Sign In</Button>
            </CardContent>
        </Card>
    )
}

export default SignIn;
